.introduce {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid #ff1493;
  border-radius: 10px;
  padding: 20px;
}

.profile-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.basic-info {
  margin: 10px 0;
}

table {
  border-collapse: collapse;
  width: 100%;
}

td {
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

td:first-child {
  font-weight: bold;
  width: 120px;
}

a {
  color: blue;
}
