.timeline {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.timeline-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.year {
  font-weight: bold;
  margin-right: 10px;
  padding: 5px 10px;
  border-radius: 10px;
  background-color: #ff1493;
  color: #fff;
}

.description {
  margin-left: 10px;
  background-color: #f8f8ff;
  padding: 10px;
  border-radius: 10px;
}
