.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  text-align: center;
}

.profile-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.basic-info {
  margin: 10px 0;
}

table {
  border-collapse: collapse;
  width: 100%;
}

td {
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

td:first-child {
  font-weight: bold;
  width: 120px;
}

a {
  color: blue;
}

.hobbies-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}
