.hobby {
  cursor: pointer;
  text-align: center;
  border: 2px solid #ff1493;
  padding: 20px;
  border-radius: 10px;
  transition: box-shadow 0.3s ease;
  background-color: #f8f8ff;
  flex-basis: calc(50% - 20px);
  max-width: 300px;
  margin-bottom: 20px;
}

.hobby:hover {
  box-shadow: 0 0 10px rgba(255, 20, 147, 0.5);
}

.hobby-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 10px;
}

.hobby-description {
  margin-top: 10px;
  font-weight: bold;
  color: #ff1493;
}
